body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #000;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.chat-window {
    display: flex;
    flex-direction: column;
    height: 80vh;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.chat-header {
    padding: 16px;
    text-align: center;
    font-size: 1.5em;
    font-weight: bold;
    background-color: #007bff;
    color: white;
    border-bottom: 1px solid #eee;
}

.message-list {
    flex: 1;
    padding: 16px;
    overflow-y: auto;
    background-color: #ffffff;
    border-bottom: 1px solid #eee;
}

.message {
    padding: 10px 20px;
    margin-bottom: 10px;
    border-radius: 20px;
    max-width: 75%;
    word-wrap: break-word;
}

.message.user {
    background-color: #dcf8c6;
    align-self: flex-end;
}

.message.bot {
    background-color: #f1f0f0;
    align-self: flex-start;
}

.message-input {
    display: flex;
    padding: 16px;
    border-top: 1px solid #eee;
    background-color: #f9f9f9;
}

.message-input input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 20px;
    margin-right: 10px;
    outline: none;
    font-size: 16px;
}

.message-input button {
    padding: 10px 20px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    outline: none;
}

.message-input button:hover {
    background-color: #0056b3;
}
